@import "https://cdnjs.cloudflare.com/ajax/libs/select2/4.0.13/css/select2.min.css";

/**
 * === SUI Overrides ===
 */
@include body-class(true) {
  #branda-dashboard-widget-summary {
    background-position: 80px center !important;
  }

  // Override wp-button shadow.
  button.sui-button {
    box-shadow: none;
    @include media(max-width, md) {
      .sui-loading-text {
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }

  // Remove transitions on sortable builder box
  .sui-box-builder {
    .sui-box-builder-body {
      .sui-builder-field.sui-can-move {
        transition: none;
      }
    }
  }

  // Show border for margin field groups.
  .branda-section-design {
    .sui-accordion {
      .sui-accordion-item-body {
        .sui-border-frame {
          &.branda_broder_group {
            border: 1px solid #e6e6e6;
            padding: 30px;
          }
        }
      }
    }
  }

  /**
	 * === SMTP Module ===
	 */
  .module-smtp-smtp-php {
    .sui-label {
      color: #666666;
      font-size: 15px;
      font-weight: 500;
      letter-spacing: -0.25px;
      line-height: 22px;
    }
    .sui-description {
      margin-bottom: 10px;
    }
    .sui-tabs-content {
      .sui-label {
        color: #aaaaaa;
        font: 500 12px/16px "Roboto", Arial, sans-serif;
      }

      .sui-description {
        margin-bottom: 0;
      }
    }
  }

  /**
	 * === Admin Help Content Module ===
	 */
  .module-admin-help-content-admin-help-content-php {
    .sui-toggle + label + .sui-description {
      margin-left: 45px;
    }
  }

  /**
	 * === Comments Control Module ===
	 */
  .module-comments-control-php {
    .branda-tab-settings-by_blacklist,
    .branda-tab-settings-by_post_type {
      padding: 0 !important;
      border: none !important;
    }

    .branda-tab-settings-by_post_type {
      ul {
        columns: 2;
      }
    }

    .sui-label {
      color: #666666;
      font-size: 15px;
      font-weight: 500;
      letter-spacing: -0.25px;
      line-height: 22px;
    }

    .sui-description {
      margin-bottom: 10px;
    }
  }

  /**
	 * === Admin Menu Module ===
	 */
  .module-admin-menu-php {
    .sui-toggle + label + .sui-description {
      margin-left: 45px;
    }
  }

  /**
	 * === Dashboard Widgets Module ===
	 */
  .module-dashboard-widgets-dashboard-widgets-php {
    .sui-label {
      color: #666666;
      font-size: 15px;
      font-weight: 500;
      letter-spacing: -0.25px;
      line-height: 22px;

      + .sui-description {
        margin-bottom: 10px;
      }
    }
  }

  /**
	 * === Dashboard Feeds Module ===
	 */
  .module-dashboard-feeds-dashboard-feeds-php {
    button.branda-dashboard-feeds-bulk {
      margin-left: 10px;
    }
    .select-container.sui-select-sm {
      .select-list-container {
        width: 160px;
      }
    }
    .sui-accordion.sui-accordion-flushed {
      border-top: none !important;
      border-bottom: none;
      .sui-accordion-header {
        padding-bottom: 0 !important;
      }
      .sui-accordion-item {
        .sui-accordion-item-header {
          .branda-feed-ellipsis {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            display: inline-block;
            max-width: 270px;
          }
          &:last-of-type {
            border-bottom: none;
          }
        }
      }
      .sui-accordion-item-body .branda-accordion-body {
        .sui-box-header {
          border-bottom: none;
          padding-top: 30px;
        }
        h3 {
          font-family: $font;
          color: #333333;
          font-size: 15px;
          font-weight: bold;
          letter-spacing: -0.25px;
          line-height: 22px;
          margin-bottom: 30px;
        }
        .branda-list-title {
          color: #333333;
          font-size: 13px;
          font-weight: 500;
          letter-spacing: -0.25px;
          line-height: 22px;
        }
        .branda-list-detail {
          color: #888888;
          font-size: 13px;
          letter-spacing: -0.25px;
          line-height: 22px;
        }
        .sui-row:not(:last-of-type) {
          .branda-divider {
            background-color: #e6e6e6;
            height: 1px;
            margin-right: 15px;
            margin-top: 30px;
            margin-left: 15px;
          }
        }
      }
    }
  }

  // Modal.
  [id^="branda-dashboard-feeds-edit"],
  #branda-dashboard-feeds-new {
    div[data-panes] {
      > div:nth-child(n + 2) {
        .sui-label {
          color: #666666;
          font-size: 15px;
          font-weight: 500;
          letter-spacing: -0.25px;
          line-height: 22px;

