          }
        }
        .sui-form-field:not(:last-of-type) {
          border-bottom: #e6e6e6 1px solid;
          padding-bottom: 30px;
        }
      }
    }
  }

  /**
	 * === Meta Widget Module ===
	 */
  .module-rebranded-meta-widget-php {
    .simple-option-description {
