// Fixes for select after upgrading to SUI 2.12.23.
.sui-wrap {
  [class*="sui-icon-"]:before,
  [class*="sui-icon-"]:after,
  .sui-box-body .sui-upsell-row .sui-upsell-notice p:first-of-type:before,
  .sui-box-selector input:checked + span:before,
  .sui-insert-variables
    .select-container
    .select-list-container
    .list-value:before,
  .sui-insert-variables
    .sui-variables
    + .select2
    .select2-selection--single
    .select2-selection__arrow:before,
  .sui-checkbox input + span:before,
  .sui-upload .sui-upload-image .sui-image-preview:before,
  .sui-upload .sui-upload-file:before,
  .sui-listing li:before,
  .sui-pagination-active-filters
    .sui-active-filter
    .sui-active-filter-remove:before,
  select.sui-select
    + .select2-container
    .select2-selection--single
    .select2-selection__arrow:before,
  .sui-select-dropdown .select2-search--dropdown:after {
    font-family: "wpmudev-plugin-icons" !important;
    speak: none;
    font-size: 1em;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    text-rendering: auto;
    display: inline-block;
    color: #888888;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  .sui-insert-variables .sui-variables + .select2 {
    width: auto !important;
    height: auto !important;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
  }

  .sui-insert-variables .sui-variables + .select2 * {
    display: block;
    outline: none;
  }

  .sui-insert-variables .sui-variables + .select2 .select2-selection--single {
    height: auto;
    border: 1px solid #dddddd;
    border-left-color: transparent;
    border-radius: 0 4px 4px 0;
    background-color: transparent;
    -webkit-transition: 0.2s ease;
    transition: 0.2s ease;
  }

  .sui-insert-variables
    .sui-variables
    + .select2
    .select2-selection--single
    .select2-selection__arrow {
    width: 38px;
    height: 38px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;
    top: auto;
    right: auto;
  }

  .sui-insert-variables
    .sui-variables
    + .select2
    .select2-selection--single
    .select2-selection__arrow:before {
    content: "@";
    display: block;
